<script>
import CustomerView from "../components/CustomerComponent.vue";
import PersonView from "../components/PersonComponent.vue";
import ClerkView from "../components/ClerkComponent.vue";
import ReservationView from "../components/ReservationComponent.vue";
import VehicleView from "../components/VehicleComponent.vue";
import InstructionsView from "../components/InstructionsViewComponent.vue";
import ReservationSystemView from "../components/RentalSystemEditableComponent.vue";
import ClassView from "../components/FactoryDiagramComponent.vue";
import PersonFactoryComponent from "../components/PersonFactoryComponent.vue";
import TestDriverComponent from "@/components/TestDriverComponent.vue";


export default {
  components: {
    CustomerView,
    PersonView,
    ClerkView,
    ReservationView,
    VehicleView,
    ReservationSystemView,
    InstructionsView,
    ClassView,
    PersonFactoryComponent,
    TestDriverComponent,

  },
  data() {
    return {
      componentSelect: "InstructionsView",
      testdriver_complete: false,
      personfactory_complete: false,
      rentalsystem_complete: false,
    };
  },
  methods: {
    testDriverCallback(value) {
      console.log("Test driver triggered", value);
      this.testdriver_complete = true;
    },
    personFactoryCallBack(value) {
      console.log("Person factory triggered", value);
      this.personfactory_complete = true;
    },
    rentalSystemCallback(value) {
      console.log("Rental system triggered", value);
      this.rentalsystem_complete = true;
    },
    instructionsReadCallback(value) {
      console.log("Instructions read", value);
      this.componentSelect = "PersonFactoryComponent"
    }
  },
};
</script>

<template>
  <main class="app">
    <button @click="componentSelect = 'InstructionsView'" :style="componentSelect === 'InstructionsView' ? 'background-color: #55acee' : ''">1. Instructions</button>
    <button @click="componentSelect = 'ClassView'" :style="componentSelect === 'ClassView' ? 'background-color: #55acee' : ''">2. Class diagram</button>
    <button @click="componentSelect = 'PersonView'" :style="componentSelect === 'PersonView' ? 'background-color: #55acee' : ''">3. Person Class</button>
    <button @click="componentSelect = 'PersonFactoryComponent'" :style="componentSelect === 'PersonFactoryComponent' ? 'background-color: #55acee' : ''">4. Person Factory Class</button>
    <button @click="componentSelect = 'CustomerView'" :style="componentSelect === 'CustomerView' ? 'background-color: #55acee' : ''">5. Customer Class</button>
    <button @click="componentSelect = 'ClerkView'" :style="componentSelect === 'ClerkView' ? 'background-color: #55acee' : ''">6. Exec Class</button>
    <button @click="componentSelect = 'ReservationView'" :style="componentSelect === 'ReservationView' ? 'background-color: #55acee' : ''">
      7. Reservation Class
    </button>
    <button @click="componentSelect = 'VehicleView'" :style="componentSelect === 'VehicleView' ? 'background-color: #55acee' : ''">8. Vehicle Class</button>
    <button @click="componentSelect = 'ReservationSystemView'" :style="componentSelect === 'ReservationSystemView' ? 'background-color: #55acee' : ''" :disabled="!personfactory_complete">
      9. Rental System Class
    </button>
    <button @click="componentSelect = 'TestDriverComponent'" :style="componentSelect === 'TestDriverComponent' ? 'background-color: #55acee' : ''" :disabled="!rentalsystem_complete">
      10. Test Driver Class </button>
    

    <KeepAlive>
      <component
          v-bind:is="componentSelect"
          @testdriver-complete="testDriverCallback"
          @personfactory-complete="personFactoryCallBack"
          @rentalsystem-complete="rentalSystemCallback"
          @instructions-read="instructionsReadCallback"
      />
    </KeepAlive>
  </main>
  <!--eslint-disable-next-line vue/no-multiple-template-root-->
  <footer v-show="(testdriver_complete && personfactory_complete && rentalsystem_complete)">
    <div id="buttons" class="relative">
      <button class="navitem" @click="$router.push('/screen-three')">Next</button>
    </div>
  </footer>
</template>

<style>
.app {
  background-color: #f9fafe;
  min-height: 1024px;
}

button {
  margin-top: 30px;
  border: none;
  padding: 20px;
  margin-left: 5px;
  background-color: #2c3e50;
  color: white;
}

button:disabled {
  background-color: #7c8e9d;
}

button:first-of-type {
  margin-left: 30px;
}
</style>
 